@import 'src/styles/variables/variables';
@import 'src/styles/mixins/contentCenterFlex';

.wrapper {
  width: 100%;
  height: 100%;
  @include contentCenterFlex;
  flex-direction: column;

  .title {
    font-size: 38px;
    margin-bottom: 20px;
    text-align: center;
    @media screen and (max-width: 420px) {
      font-size: 32px;
    }
  }
  .linkContainer {
    max-width: 420px;
    display: flex;
    justify-content: space-around;
    @media screen and (max-width: 440px) {
      flex-direction: column;
      align-items: center;
    }
    .start {
      @include contentCenterFlex;
      width: 180px;
      height: 40px;
      border: none;
      border-radius: $primary-border-radius;
      background: $primary-background;
    }
    .choice {
      display: block;
      margin: 4px 10px;
      font-size: 36px;
    }
    .linkToLibrary {
      @include contentCenterFlex;
      width: 180px;
      height: 40px;
      border: none;
      color: $primary-color;
      border-radius: $primary-border-radius;
      background: #540adc;
    }
  }

  .allBooksLink {
    width: 180px;
    height: 32px;
    border: 1px solid black;
    margin: 60px 20px 40px;
  }
}
